"use client";

import { useRouter } from "next/navigation";
import { useState } from "react";

export default function ContactForm() {
  const router = useRouter();
  const [loading, setLoading] = useState(false);

  async function onSubmit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    setLoading(true);

    setTimeout(() => {
      router.push("/thank-you");
    }, 350);
  }

  return (
    <form onSubmit={onSubmit} className="card cardPad form">
      <div className="grid" style={{ gridTemplateColumns: "1fr", gap: 14 }}>
        <div className="grid" style={{ gap: 14 }}>
          <Field label="Name" name="name" type="text" required />
          <Field label="Email" name="email" type="email" required />
        </div>

        <div className="grid" style={{ gap: 14 }}>
          <Field label="Phone" name="phone" type="tel" />

          <div className="field">
            <div className="label">Buyer or Seller</div>
            <select className="select" name="intent" defaultValue="Buyer">
              <option>Buyer</option>
              <option>Seller</option>
              <option>Both</option>
              <option>Relocation</option>
            </select>
          </div>
        </div>

        <div className="field">
          <div className="label">Timeline</div>
          <select className="select" name="timeline" defaultValue="0-3 months">
            <option>0–3 months</option>
            <option>3–6 months</option>
            <option>6–12 months</option>
            <option>Just researching</option>
          </select>
        </div>

        <div className="field">
          <div className="label">Message</div>
          <textarea
            className="textarea"
            name="message"
            rows={5}
            placeholder="What would make this move a win for you?"
          />
        </div>
      </div>

      <div style={{ display: "flex", flexDirection: "column", gap: 10, marginTop: 8 }}>
        <button type="submit" className="btn btnPrimary" disabled={loading}>
          {loading ? "Sending…" : "Send Message & Book"} <span aria-hidden style={{ opacity: 0.7 }}>→</span>
        </button>
        <div className="small">Prefer a direct booking? Scroll to the calendar below.</div>
      </div>

      <style>{`
        @media (min-width: 900px) {
          form .grid { grid-template-columns: 1fr 1fr; }
          form .grid > .grid { grid-template-columns: 1fr; }
          form .field:nth-child(3), form .field:nth-child(4) { grid-column: 1 / -1; }
        }
      `}</style>
    </form>
  );
}

function Field({
  label,
  name,
  type,
  required,
}: {
  label: string;
  name: string;
  type: string;
  required?: boolean;
}) {
  return (
    <div className="field">
      <div className="label">{label}</div>
      <input className="input" name={name} type={type} required={required} />
    </div>
  );
}
